
body{
  margin:0;
}
.App2{
margin-top:-18px;
width: 100%;

-webkit-animation: 15s infinite linear;

        animation: 15s infinite linear;
-webkit-animation-name: sky2;
        animation-name: sky2;
}

.Containerlevel4{
display: -ms-flexbox;
display: flex;
width: 100%;
height: 100vh;
-ms-flex-direction: column;
    flex-direction: column;
-ms-flex-pack: center;
    justify-content: center;
-ms-flex-align: center;
    align-items: center;
-ms-flex-wrap: wrap;
    flex-wrap: wrap;

}
.ball_level4{
       background-image: url(./img/15596_a4e35f4989603d24a0ff672c034056b7ba6a8415.jpeg);
        background-size: cover;
        background-position-x: -20px;
        width: 50px;
       height: 50px;
       border-radius: 50%;
       border: 4px solid #554f4f99;
       -webkit-animation: movelevel4  infinite 3s linear;
               animation: movelevel4  infinite 3s linear;
}
.Tap{
    -ms-flex-direction: row;
        flex-direction: row;  
}


.ball_level4:hover{
    border: 4px solid rgba(29, 156, 93, 0.6);
           -webkit-transform: 2s;
               -ms-transform: 2s;
                   transform: 2s;
     width: 50px;
       height: 50px;
   

}
.MPS_level4{
          background-size: cover;

         width: 50px;
       height: 50px;
       border-radius: 50%;
       border: 4px solid rgba(85, 79, 79, 0.6);
      -webkit-animation-delay: 2s;
       animation-delay:2s;
     -webkit-animation: movelevel4  infinite 5s linear;
             animation: movelevel4  infinite 5s linear;
      background-image: url(./img/20160719_211138.png);

}

      @-webkit-keyframes movelevel4 {
        
    
      0% {
    -webkit-transform: translate(0%);
            transform: translate(0%);
    border-radius: 50%;
  }
  25% {
    -webkit-transform: translate(100%) scale(0.5);
            transform: translate(100%) scale(0.5); 
    border-radius: 0%;
  }
  50% {
    -webkit-transform: translate(-250%, 50%);
            transform: translate(-250%, 50%);
    border-radius: 50%; 
  }
  75% {
    -webkit-transform: translate(200%, 100%) scale(0.5);
            transform: translate(200%, 100%) scale(0.5); 
    border-radius: 0%;
  }
    95% {
    -webkit-transform: translate(0%, 100%) scale(0.5);
            transform: translate(0%, 100%) scale(0.5); 
    border-radius: 0%;
  }
}

      @keyframes movelevel4 {
        
    
      0% {
    -webkit-transform: translate(0%);
            transform: translate(0%);
    border-radius: 50%;
  }
  25% {
    -webkit-transform: translate(100%) scale(0.5);
            transform: translate(100%) scale(0.5); 
    border-radius: 0%;
  }
  50% {
    -webkit-transform: translate(-250%, 50%);
            transform: translate(-250%, 50%);
    border-radius: 50%; 
  }
  75% {
    -webkit-transform: translate(200%, 100%) scale(0.5);
            transform: translate(200%, 100%) scale(0.5); 
    border-radius: 0%;
  }
    95% {
    -webkit-transform: translate(0%, 100%) scale(0.5);
            transform: translate(0%, 100%) scale(0.5); 
    border-radius: 0%;
  }
}
  





.ball1{
  -webkit-animation-delay: 1.5s;
  animation-delay: 1.5s;
}
.ball2{
    -webkit-animation-delay: 1s;
  animation-delay: 1s;
}
.ball3{
    -webkit-animation-delay: 0.5s;
  animation-delay: 0.5s;  
}
.ball4{
    -webkit-animation-delay: 2s;
  animation-delay: 2s;
}
.ball5{
    -webkit-animation-delay: 3s;
  animation-delay: 3s;
}


