.App-logo {
  -webkit-animation: App-logo-spin infinite 10s linear;
          animation: App-logo-spin infinite 10s linear;
  height: 50%;
  position: absolute;
  border-radius: 50%;
}



@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}


.Chill{
  height: 100vh;
  width: 100%;
    background: black;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
  color: white;
overflow: hidden;
}

.Chill_logo{
display: -ms-flexbox;
display: flex;
-ms-flex-pack: center;
    justify-content: center;  
color: white;
list-style: none;
font-size: 8em;
-webkit-transition: all 8s;
-o-transition: all 8s;
transition: all 8s;
opacity: 0;
margin-left: -5%;
-webkit-transition: all 2s;
-o-transition: all 2s;
transition: all 2s;
  z-index: 1;
}
.Chill_logo li{
text-shadow: 5px 40px 30px rgb(255, 255, 255);
-webkit-transition: -webkit-transform 5s;
transition: -webkit-transform 5s;
-o-transition: transform 5s;
transition: transform 5s;
transition: transform 5s, -webkit-transform 5s;
-webkit-transform: translate(0px, 0px, 0px);
    -ms-transform: translate(0px, 0px, 0px);
        transform: translate(0px, 0px, 0px);
margin-left: 2%;
}
.Chill_logo li:first-child{
color: rgb(0, 220, 228);
 text-shadow: 5px 40px 30px rgb(14, 143, 194);
}
.Chill_run li:first-child{
  -webkit-transform: translate(-50px, -80px, 0px);
      -ms-transform: translate(-50px, -80px, 0px);
          transform: translate(-50px, -80px, 0px);

}
.Chill_run li:nth-child(2){
  -webkit-transform: translate3d(0px, 80px, 0px);
          transform: translate3d(0px, 80px, 0px);
}
.Chill_run li:nth-child(4){
  -webkit-transform: translate3d(0px, -150px, 0px);
          transform: translate3d(0px, -150px, 0px);
}
.Chill_run li:nth-child(5){
  -webkit-transform: translate3d(120px, 50px, 0px);
          transform: translate3d(120px, 50px, 0px);
}
.Chill_run li:nth-child(6){
  -webkit-transform: translate3d(50px, -70px, 0px);
          transform: translate3d(50px, -70px, 0px);
}
.Chill_run li:nth-child(7){
  -webkit-transform: translate3d(120px, 50px, 0px);
          transform: translate3d(120px, 50px, 0px);
}

.Chill_autor{
font-size: 18px;
opacity: 0;
-webkit-transition: all 3s;
-o-transition: all 3s;
transition: all 3s;
width: auto;
}
.Chill_ball{
  width: 50px;
  height: 50px;
  border-radius: 50%;
-webkit-animation: Chill_ball 3s linear infinite alternate;
        animation: Chill_ball 3s linear infinite alternate;
  background: rgb(255, 255, 255);
  position: absolute;
opacity: 0;
}
.Chill_ball:first-child{
  left:10%;
}
.Chill_ball:nth-child(2){
    background: rgb(195, 214, 21);
    left:20%;
    top:10%;
    -webkit-animation-delay: 1.5s;
            animation-delay: 1.5s;
}
.Chill_ball:nth-child(3){
    background: rgb(59, 56, 245);
     right:20%;
    top:20%;
      width: 50px;
  height: 50px;
        -webkit-animation-delay: 5.5s;
                animation-delay: 5.5s;
}
.Chill_ball:nth-child(4){
    background: rgb(151, 14, 144);
    bottom:30%;
         width: 30px;
  height: 30px;
  right: 10%;
      -webkit-animation-delay: 3.0s;
              animation-delay: 3.0s;
}


.Chill_autor{
  text-shadow: 5px 5px 3px rgb(255, 255, 255);
}
@media screen and (max-width: 465px) {
.Chill_logo{
font-size: 3em;
}
.App-logo {

  height: 30%;


}
.Chill_autor{
  position: absolute;
top:80%;
}
}
@-webkit-keyframes Chill_ball{
  from {
    background: rgb(0, 0, 0);
     width: 15px;
  height: 15px }
  to {    opacity: 1; }
}
@keyframes Chill_ball{
  from {
    background: rgb(0, 0, 0);
     width: 15px;
  height: 15px }
  to {    opacity: 1; }
}





 




