.Anime{
width: 80px;
height: 80px;
margin: 0 auto;
 border: 3px solid rgba(85, 85, 85, 0.9);
-webkit-box-shadow:3px solid rgba(85, 85, 85, 0.9);
        box-shadow:3px solid rgba(85, 85, 85, 0.9);
display: -ms-flexbox;
display: flex;
-ms-flex-align: center;
    align-items: center;
-ms-flex-pack: center;
    justify-content: center;
-ms-flex-direction: row;
    flex-direction: row;
-webkit-animation: Anime 3s  infinite linear alternate;
        animation: Anime 3s  infinite linear alternate;

}
.Anime:hover{

}

@-webkit-keyframes Anime{

      from { -webkit-transform: rotate(0deg) ; transform: rotate(0deg) ;
     }
      
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes Anime{

      from { -webkit-transform: rotate(0deg) ; transform: rotate(0deg) ;
     }
      
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



